  font-size:20px;

}

.mdl-demo .mdl-header-icon-cell
{
  height: auto; 
  alignment-baseline: central;
  align-content: center;
 
  align-items: center;
  display: flex;
 padding: 24px;
  /*max-width: 256px; */
  min-width: 128px; 
}


